.apps {
  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    padding: 0 10px;
    &--left {
      display: flex;
      align-items: center; }
    &--right {
      display: flex;
      align-items: center; }
    &__title {
      font-family: $Archive;
      font-weight: 400;
      font-size: 14px;
      color: #06d6a0;
      display: flex;
      align-items: center; }
    &__add {
      cursor: pointer;
      transition: .2s ease-in-out;
      display: inline-block;
      width: 20px;
      height: 20px;
      background-image: url("../images/icons/add.svg");
      background-size: contain;
      background-position: center;
      &:hover {
        opacity: .5; } }
    &__refresh {
      cursor: pointer;
      width: 26px;
      height: 26px;
      margin-left: 5px;
      background-image: url("../images/icons/refresh.svg");
      background-position: center;
      background-size: 60%;
      background-repeat: no-repeat; } }
  &__body {
    &__list {
      &-item {
        box-sizing: border-box;
        width: 100%;
        background-color: #fff;
        margin-bottom: 10px;
        border-radius: 10px;
        box-shadow: 0 2px 5px 0 #e6e6e6;
        transition: .2s box-shadow ease-in-out;
        overflow: hidden;
        &:hover {
          box-shadow: 0 2px 5px 0 #06d6a0; }
        &:last-child {
          margin-bottom: 0; }
        &__head {
          display: flex;
          padding: 5px 10px;
          background: #fff;
          border-radius: 10px 10px 0 0;
          box-shadow: 0 2px 5px 0 #e6e6e6;
          &-item {
            display: flex;
            font-family: $Archive;
            font-size: 10px;
            color: #394649;
            &[data-section='logo'] {
              width: 50px; }
            &[data-section='name'] {
              width: 470px;
              padding-left: 20px;
              box-sizing: border-box;
              justify-content: flex-start; }
            &[data-section='status'] {
              width: 100px;
              justify-content: center; }
            &[data-section='more'] {
              width: 144px;
              justify-content: flex-end; } } }
        &__main {
          display: flex;
          height: 50px;
          padding: 10px;
          &-item {
            &[data-section='logo'] {
              width: 50px;
              img {
                max-width: 100%;
                max-height: 100%; } }
            &[data-section='name'] {
              width: 470px;
              padding-left: 20px;
              box-sizing: border-box;
              display: flex;
              align-items: center;
              font-family: $FiraSans;
              font-weight: 400;
              font-size: 14px;
              color: #394649;
              span {
                color: #d3d3d3; } }
            &[data-section='status'] {
              width: 100px;
              display: flex;
              align-items: center;
              justify-content: center;
              span {
                font-family: $FiraSansSemiBold;
                font-weight: 500;
                font-size: 14px;
                color: #ff4661;
                &.active {
                  color: #06d6a0; } } }
            &[data-section='more'] {
              width: 144px;
              display: flex;
              align-items: center;
              justify-content: flex-end;
              button {
                cursor: pointer;
                border: 0;
                padding: 0;
                margin: 0;
                width: 30px;
                height: 30px;
                background-color: transparent;
                background-image: url("../images/icons/more.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: 70%;
                transition: .2s opacity ease-in-out;
                &:hover {
                  opacity: .5; } } } } }
        &__dropdown {
          display: none;
          padding: 10px;
          justify-content: space-between;
          position: relative;
          &.visible {
            display: flex;
            align-items: flex-start; }
          &:before {
            content: '';
            position: absolute;
            left: 10px;
            right: 10px;
            top: 0;
            height: 1px;
            background-color: #b3bbbd; }
          &__info {
            &-main {
              margin-bottom: 15px;
              &__item {
                display: flex;
                align-items: center;
                font-family: $FiraSans;
                font-weight: 400;
                font-size: 14px;
                color: #394649;
                margin-bottom: 5px;
                &:last-child {
                  margin-bottom: 0; }
                span {
                  font-family: $FiraSansSemiBold;
                  font-weight: 500; }
                &__edit {
                  display: inline-block;
                  margin-left: 5px;
                  width: 12px;
                  height: 12px;
                  cursor: pointer;
                  background-repeat: no-repeat;
                  background-size: 90%;
                  background-position: center;
                  background-image: url("../images/icons/edit.svg"); } } }
            &-tech {
              &__item {
                font-family: $FiraSans;
                font-weight: 400;
                font-size: 14px;
                color: #394649;
                margin-bottom: 5px;
                &:last-child {
                  margin-bottom: 0; }
                span {
                  font-family: $FiraSansSemiBold;
                  font-weight: 500; } } } }
          &__links {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            &-item {
              display: flex;
              align-items: center;
              margin-right: 10px;
              &:last-child {
                margin-right: 0; }
              a {
                cursor: pointer;
                border: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                text-transform: uppercase;
                padding: 0 10px;
                height: 26px;
                font-family: $Archive;
                font-weight: 400;
                font-size: 12px;
                color: #fff;
                border-radius: 22px;
                background-color: #00e7d5;
                box-shadow: 0 4px 5px 0 rgba(0, 231, 213, 0.6); } } } } } }
    &__placeholder {
      font-family: $FiraSans;
      font-weight: 400;
      text-align: center; } }
  &-popup {
    width: 490px !important;
    padding: 60px 50px 50px !important;
    box-sizing: border-box !important;
    border: 0 !important;
    background-color: #ffffff !important;
    box-shadow: 0 2px 5px 0 #e6e6e6 !important;
    &__main {
      margin-bottom: 30px;
      &__title {
        font-family: $Archive;
        font-size: 28px;
        text-align: center;
        color: #67777b; }
      &__list {
        margin-top: 15px;
        &-item {
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0; }
          &__title {
            font-family: $FiraSansSemiBold;
            font-weight: 500;
            font-size: 14px;
            padding-bottom: 5px; }
          &__value {
            font-family: $FiraSans;
            font-weight: 400;
            font-size: 16px;
            span {
              color: #00e7d5; } } } } }
    &__btn-block {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-top: 20px;
      &--center {
        justify-content: center; }
      &__btn {
        cursor: pointer;
        border: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        padding: 0 20px;
        height: 44px;
        font-family: $Archive;
        font-weight: 400;
        font-size: 16px;
        color: #fff;
        border-radius: 22px;
        background-color: #00e7d5;
        box-shadow: 0 4px 5px 0 rgba(0, 231, 213, 0.6);
        &--cancel {
          cursor: pointer;
          margin-left: 40px;
          font-family: $FiraSans;
          font-weight: 400;
          font-size: 16px;
          color: #006f9e;
          border-bottom: 1px solid #006f9e; } } } } }
