.control {
  position: fixed;
  z-index: 100000;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
  background-color: #131C2B;
  padding: 20px;
  color: #fff;
  box-sizing: border-box;
  @media only screen and (max-width: 1023px) {
    display: none; }
  &__logo {
    height: 40px;
    margin-bottom: 20px;
    background-image: url("../images/icons/pm_logo_new.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat; }
  &__web3isNotConnect {
    font-family: $FiraSansThin;
    font-weight: 200;
    font-size: 16px;
    color: #fff;
    text-align: center; }
  &__web3 {
    &-title {
      font-family: $Archive;
      font-weight: 400;
      font-size: 14px;
      text-align: center;
      margin-bottom: 10px; }
    &__list {
      &-item {
        display: flex;
        align-items: center;
        &__typical {
          font-family: $FiraSans;
          font-weight: 400;
          font-size: 14px;
          margin-bottom: 5px;
          span {
            font-family: $FiraSansSemiBold;
            font-weight: 500;
            color: #00e7d5; } }
        &__price {
          padding-top: 10px;
          font-family: $FiraSansSemiBold;
          font-weight: 500;
          font-size: 14px;
          margin-bottom: 5px;
          span {
            color: #00e7d5;
            display: inline-block;
            text-align: right; }
          button {
            display: block;
            padding: 0;
            margin: 0 0 0 5px;
            width: 10px;
            height: 10px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../images/icons/refresh-green.svg");
            box-shadow: none;
            box-sizing: border-box;
            background-color: transparent;
            border: 0;
            cursor: pointer;
            &:hover {
              opacity: .7; } } }
        &__input {
          font-size: 0;
          margin-bottom: 10px;
          input {
            margin: 0;
            padding: 0;
            width: 100%; } }
        &__text {
          font-family: $FiraSansThin;
          font-weight: 200;
          font-size: 12px;
          line-height: 14px;
          margin-bottom: 10px; }
        &__warning {
          font-family: $FiraSans;
          font-weight: 400;
          font-size: 12px; } } }
    &__placeholder {
      //font-family: 'Fira Sans Thin', sans-serifsee
      font-family: $FiraSansThin;
      font-weight: 200;
      font-size: 16px;
      color: #fff;
      text-align: center; } }
  &__nodes {
    margin-top: 20px;
    &-title {
      font-family: $Archive;
      font-weight: 400;
      font-size: 14px;
      text-align: center;
      margin-bottom: 10px; }
    &__list {
      &-item {
        font-family: $FiraSansSemiBold;
        font-weight: 500;
        font-size: 12px;
        padding-bottom: 3px;
        span {
          font-family: $FiraSans;
          font-weight: 400;
          font-size: 12px; } } }
    &__placeholder {
      font-family: $FiraSansThin;
      font-weight: 200;
      font-size: 16px;
      color: #fff;
      text-align: center; } } }

